import HomePage from '@/views/Home'

const routes = [
    {
        path: '/',
        redirect: { path: '/home' }
    },
    {
        path: '/login',
        component: () => import('@/views/Login.vue'),
        meta: {
            title: '',
            hideInMenu: true
        }
    },
    {
        path: '/home',
        component: HomePage,
        meta: {
            title: '',
            hideInMenu: true
        },
        children: [
            {
                path: '/main',
                component: () => import('@/views/Main.vue'),
                name: 'Main',
                meta: {
                    title: '',
                    showInFirst: true,
                    icon: 'home'
                }
            },
            {
                path: '/system',
                component: HomePage,
                meta: {
                    title: '',
                    icon: 'setting'
                },
                children: [
                    {
                        path: '/userManage',
                        component: () => import('@/views/system/user/Manage'),
                        name: 'UserManagePage',
                        meta: {
                            title: '',
                            icon: 'user'
                        }
                    }
                ]
            },
        ]
    }
]

export default routes;